import React, { Component } from "react";

export default class DocPrivacy extends Component {
  render() {
    return (
        <div>
            <h1>Poltica de privacidade para Lino Bot</h1>
            <p>Todas as suas informaes pessoais recolhidas, sero usadas para o ajudar a tornar a sua visita no nosso site o mais produtiva e agradvel possvel.</p>
            <p>A garantia da confidencialidade dos dados pessoais dos utilizadores do nosso site  importante para o linobot.</p>
            <p>Todas as informaes pessoais relativas a membros, assinantes, clientes ou visitantes que usem o linobot sero tratadas em concordncia com a Lei da Proteo de Dados Pessoais de 26 de outubro de 1998 (Lei n. 67/98).</p>
            <p>A informao pessoal recolhida pode incluir o seu nome, e-mail, nmero de telefone e/ou telemvel, morada, data de nascimento e/ou outros.</p>
            <p>O uso do linobot pressupe a aceitao deste Acordo de privacidade. A equipe do linobot reserva-se ao direito de alterar este acordo sem aviso prvio. Deste modo, recomendamos que consulte a nossa poltica de privacidade com regularidade de 
            forma a estar sempre atualizado.</p>
            <h2>Os anncios</h2>
            <p>Tal como outros websites, coletamos e utilizamos informao contida nos anncios. A informao contida 
            nos anncios, inclui o seu endereo IP (Internet Protocol), o seu ISP (Internet Service Provider, como o Sapo, Clix, ou outro), o browser que utilizou ao visitar 
        o nosso website (como o Internet Explorer ou o Firefox), o tempo da sua visita e que pginas visitou dentro do nosso website.</p>
            <h2>Ligaes a Sites de 
            terceiros</h2>
            <p>O linobot possui ligaes para outros sites, os quais, a nosso ver, podem conter informaes / ferramentas teis para os nossos visitantes. A 
            nossa poltica de privacidade no  aplicada a sites de terceiros, pelo que, caso visite outro site a partir do nosso dever ler a politica de privacidade do 
            mesmo.</p>
            <p>No nos responsabilizamos pela poltica de privacidade ou contedo presente nesses mesmos sites.</p>
        </div>
    );
  }
}



